import Styled, { keyframes } from 'styled-components';

const fadeOut = keyframes`
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
`;

const fadeInUp = keyframes`  
  0% {
    opacity: 0;
    transform: translate3d(0, 20px, 0);
  }
  10% {
    opacity: 0;
    transform: translate3d(0, 20px, 0);
  }
  100% {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
`;

const fadeInDown = keyframes`

  0% {
    opacity: 0;
    transform: translate3d(0, -20px, 0);
  }
  10% {
    opacity: 0;
    transform: translate3d(0, -20px, 0);
  }
  100% {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
`;

export const ToolbarLayoutWrapper = Styled.div`
  padding: 4px 0;
  &.fit-content {
    display: inline-flex
  }
  .wdn-toolbar-content {
    display: flex;
    align-items: center;
    .wdn-toolbar-items {
      box-sizing: content-box;
      &.singleLine {
        height: 28px;
        padding: 4px 0;
      }
      &.doubleLine {
        height: 52px;
      }
      position: relative;
      flex-grow: 1;
    }

    .wdn-icon-switch-wrapper {
      line-height: 0;
      &:hover {
        background-color: rgba(25,31,37,0.08);
      }
      &:active {
        background-color: rgba(25,31,37,0.16);
      }
      border-radius: 4px;
    }

    .wdn-icon-switch {
      transition: transform 300ms;
      transform: rotate(0deg);
      &.rotate {
        transform: rotate(180deg);
      }
    }
  }

  .toolbar-left-enter.single {
    animation-duration: 300ms;
    animation-fill-mode: forwards;
    animation-name: ${fadeInUp};
    z-index: 2;
  }

  .toolbar-left-enter.double {
    animation-duration: 300ms;
    animation-fill-mode: forwards;
    animation-name: ${fadeInDown};
    z-index: 2;
  }

  .toolbar-left-leave.single,
  .toolbar-left-leave.double {
    animation-duration: 30ms;
    animation-fill-mode: forwards;
    animation-name: ${fadeOut};
    top: 0;
    position: absolute;
    z-index: 1;
  }
`;